import React, { Component } from "react";

class Menu extends Component {
    render() {
        const { data, currentPage } = this.props;
        console.log("atd", data);
        return (
            <li
                className={
                    `mx-2 header-menu nav-item ${
                        data.type === "dropdown" && "dropdown"
                    }` + (currentPage === data.link ? " active" : "")
                }
            >
                {data.src !== "gomakeimpact" &&
                    data.src !== "superapp" &&
                    (data.type === "dropdown" ? (
                        <React.Fragment>
                            <a
                                className="nav-link dropdown-toggle"
                                href="#"
                                id="navbarDropdown"
                                role="button"
                                data-toggle="dropdown"
                                aria-haspopup="true"
                                aria-expanded="false"
                            >
                                News &amp; Videos
                            </a>
                            <div
                                className="dropdown-menu"
                                aria-labelledby="navbarDropdown"
                            >
                                {data.options.map((item, i) => {
                                    return (
                                        <a
                                            key={i}
                                            className="dropdown-item navbar-dropdown-item text-muted"
                                            href={item.link}
                                        >
                                            {item.name}
                                        </a>
                                    );
                                })}
                            </div>
                        </React.Fragment>
                    ) : (
                        <a
                            className={
                                data.type === "button"
                                    ? `header-btn btn btn-success rounded-1 maison-bold green-apply-btn`
                                    : "nav-link"
                            }
                            href={data.link}
                            target={
                                data.type === "externalLink" ? "_blank" : null
                            }
                        >
                            {data.name}
                            {currentPage === data.link ? (
                                <span className="sr-only">(current)</span>
                            ) : (
                                ""
                            )}
                            {data.type === "externalLink" && (
                                <img
                                    width="18px"
                                    className="img-fluid pb-1"
                                    src="../../../../images/logo-gif.gif"
                                />
                            )}
                        </a>
                    ))}
            </li>
        );
    }
}

export default Menu;
